  .card {
  background: #fff;
  border-radius: 15px;
  height: 90%;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(220, 194, 108);
  border-radius: 15px;
}
.card > .img-container > img {
  position: relative;
  object-fit: contain;
  top: 50%;
  width: 80%;  
  height: 90%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);        
}


@media all and (min-width: 1200px) {
  .card > .img-container > img {
    width: 65%;       
  }
}

#menu {
  height: 100%;
  width: 100%;
}

#menu p {
  margin: 0;
}

#logo { 
  position: absolute;
  width: 50%;
  left: 50%;
  top: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

#logo-background {
  position: absolute;
  height: 100vw;
  width: 30vh;  
  left: -26.5%;
  top: 6%;
  -webkit-transform: rotate(90deg) translate(-50%, -50%);
          transform: rotate(90deg) translate(-50%, -50%);
  z-index: -1;
}

#logo-text {
  position: absolute;
  top: 3%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); 
  color: white;
}

#menu-text {  
  margin-top: 34%;
  margin-bottom: 25%;  
  padding: 0 10px;  
  font-size: 2em;    
  text-align: center;
  color: #5a4827;
}


@media all and (min-width: 576px) {
  #menu {
    max-width: 370px;
    position: relative;
  }

  #logo {
    width: 110%; 
    position: absolute; 
    left: -5%; 
    top: 0; 
    -webkit-transform: unset; 
            transform: unset;
  }

  #logo-background {
    position: relative;
    height: 100%; 
    width: 100%;
    left: 0;  
    top: 0;
    -webkit-transform: unset;
            transform: unset;
  }

  #logo-text {
    width: 100%; 
    height: logo; 
    position: absolute; 
    top: 0; 
    left: 0; 
    font-size: 0.625em; 
    text-align: center; 
    padding-top: 12%;     
    -webkit-transform: unset;     
            transform: unset;
  }

  #logo-text h1 {
    font-size: 2.5em;
  }

  #logo-text h3 {
    font-size: 1.75em;
  }

  #menu-text {
    width: 100%; 
    position: absolute; 
    margin-top: 0;
    margin-bottom: 0;
    bottom: 10%; 
    left: 50%; 
    -webkit-transform: translate(-50%, 0); 
            transform: translate(-50%, 0); 
    text-align: center; 
    font-size: 1em;         
  }

  #menu-text p:first-of-type {
    padding: 0 20%;
  }
}
@media all and (min-width: 576px) {
  .container-fluid {
    height: 100%;
  }

  .container-fluid > .row {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);    
  }
}
* {
  box-sizing: border-box;
  font-family: 'Acme', sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

body {
  font-size: 2vw;
}


